:root {
  --index: calc(1vw + 1vh);
  --transition: ease;
  --white: #FFF;
  --site-white: #FFFFF0;
  --link: rgba(54, 56, 56, 0.9033);
  --gray: #DFDFDF;
  --title: #363838;
  --default: #5A5A5A;
  --accent: #f1d152;
  --dove-gray: #707070;
  --line: #979797;
  --top-bg: #F1D152;
  --line1: #6D7278;
  --partners-fill: rgb(151, 151, 151, .7);
  --bg-play-btn: rgba(54, 56, 56, 0.4);
  --copyright: #A1A1A1;
  --tags-blog: #8F8D8D;
  --works-path-line: rgba(54, 56, 56, 0.7);
  --black: #000000;
  --bs: -2px 3px 17px 0 #000;
  --font-family: "OpenSans", sans-serif;
  --second-family: "Montserrat", sans-serif;
  --text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  --bg-fill: rgba(255, 255, 255, .6);
  --br: 14px;
  --border-radius: 10px;
  --space-sm: 0.75em;
}

.subtitle__fourth, .subtitle__five, .rightside-menu__title, .menu__link, .footer__info-title {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
}

.menu__item:before, .menu__item:after, .menu__link:before {
  content: "";
  position: absolute;
  height: 33.33%;
  width: 0%;
  background-color: var(--accent);
  transition: 0.45s ease;
}

.header {
  padding-block: clamp(0.25rem, -0.155rem + 2.03vw, 1.25rem);
  box-shadow: var(--bs);
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 10;
  opacity: 1;
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.header__inner.hidden {
  z-index: 0;
  opacity: 0;
}
.header__btn {
  padding: 10px;
  color: var(--black);
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .header__btn:hover {
    transform: scale(1.2);
  }
}
.header__btn-menu {
  display: none;
}
.header__btn-menu span {
  display: block;
  width: 30px;
  height: 3px;
  background-color: var(--title);
  border-radius: 4px;
  margin-bottom: 6px;
}
@media screen and (width < 1024px) {
  .header__btn {
    display: none;
  }
}
@media screen and (width < 541px) {
  .header__inner-btn--open {
    flex-wrap: wrap;
  }
  .header__inner-btn--open .header__btn-menu {
    order: -1;
  }
  .header__btn-menu {
    display: block;
    position: relative;
    z-index: 12;
  }
}

.footer__content {
  padding: 60px 0 75px;
  background-color: var(--gray);
}
.footer__inner {
  display: flex;
  gap: 104px;
}
.footer__info {
  width: 540px;
}
.footer__info-title {
  margin-bottom: 15px;
  font-size: 16px;
  line-height: 169%;
  color: var(--title);
}
.footer__info-text {
  margin-bottom: 16px;
  max-width: 476px;
}
.footer__form {
  position: relative;
  display: flex;
  gap: 25px;
}
.footer__form-alert {
  position: absolute;
  top: 0;
  width: 100%;
  border-radius: 16px;
  border: 2px solid var(--link);
  text-align: center;
  padding: 10px;
  left: 1%;
  background-color: var(--site-white);
  box-shadow: -2px 3px 17px 0 #000;
}
.footer__form-input {
  width: 100%;
  padding: 6px 10px 6px 18px;
  border-radius: 21px;
  background-color: transparent;
  border: 1px solid rgba(54, 56, 56, 0.7);
}
.footer__form-input::placeholder {
  font-family: "OpenSans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6875;
  color: var(--copyright);
}
.footer__nav {
  display: flex;
  gap: clamp(3.125rem, -50.431rem + 86.21vw, 10rem);
}
.footer-social {
  display: flex;
  flex-direction: column;
  gap: 3px;
}
.footer-social__item {
  display: inline-block;
  width: 100%;
  cursor: pointer;
}
.footer-social__link {
  padding-block: 6px;
  width: max-content;
  display: flex;
  align-items: center;
  gap: 12px;
}
.footer__email {
  margin-bottom: 22px;
}
.footer__menu {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
@media screen and (width > 822px) {
  .footer__menu {
    width: 135px;
  }
}
.footer__menu-link {
  padding-block: 3px;
}
.footer__copyright {
  padding: 42px 0 42px;
  background-color: var(--title);
  color: var(--copyright);
  text-align: center;
}
@media screen and (width < 993px) {
  .footer__info {
    width: 380px;
  }
  .footer__form {
    flex-direction: column;
  }
}
@media screen and (width < 823px) {
  .footer__content {
    padding: 30px 0 50px;
  }
  .footer__inner {
    flex-wrap: wrap;
  }
  .footer__info {
    width: 100%;
    text-align: center;
  }
  .footer__info-text {
    max-width: 100%;
  }
  .footer__nav {
    flex: auto;
  }
  .footer__menu {
    max-width: 100%;
    margin-left: auto;
  }
  .footer-social {
    max-width: 100%;
  }
}
@media screen and (width < 483px) {
  .footer__form {
    display: block;
  }
  .footer__form-input {
    margin-bottom: 15px;
  }
}
@media screen and (width > 822px) {
  .footer-social {
    width: 125px;
  }
}

.menu__list {
  display: flex;
  gap: 60px;
}
.menu__list--margin {
  margin-left: 32px;
  margin-right: 60px;
}
.menu__list--margin-right {
  margin-right: 44px;
}
.menu__item {
  position: relative;
  height: 100%;
}
.menu__item:before {
  right: 0;
  bottom: 33.33%;
}
.menu__item:after {
  left: 0;
  bottom: 0;
}
@media (any-hover: hover) {
  .menu__item:hover::before {
    width: 100%;
  }
  .menu__item:hover::after {
    width: 100%;
  }
  .menu__item:hover .menu__link {
    text-shadow: 1px 1px 5px var(--black);
  }
  .menu__item:hover .menu__link::before {
    width: 100%;
  }
}
.menu__link {
  position: relative;
  padding-block: 20px;
  padding-inline: 10px;
  line-height: 1.375;
  text-align: center;
  color: var(--link);
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 1;
}
.menu__link:before {
  left: 0;
  bottom: 66.66%;
}
.menu__link:focus-visible {
  border-radius: 10px;
  outline: 0;
  box-shadow: 0 0 10px var(--accent), 0 0 5cap var(--title);
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.menu__link.active {
  background-color: var(--accent);
}
@media screen and (width < 1024px) {
  .menu__list--margin {
    margin-left: 0;
    margin-right: 0;
  }
  .menu__list--margin-right {
    margin-right: 0;
  }
}
@media screen and (width < 769px) {
  .menu__list {
    gap: 20px;
  }
  .menu__link--active::after {
    bottom: 0;
  }
}
@media screen and (width < 541px) {
  .menu {
    visibility: hidden;
    opacity: 0;
    padding-right: 0;
    width: 100%;
    background-color: white;
    position: absolute;
    top: 20px;
    z-index: 11;
    transform: translateY(-350px);
    transition-duration: 550ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    animation: opac 0.7s;
  }
  @keyframes opac {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  .menu--open {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    visibility: visible;
    opacity: 1;
    transform: translateY(40px);
    -webkit-backdrop-filter: blur(19px);
    backdrop-filter: blur(19px);
    background-color: rgba(255, 255, 255, 0.04);
    border: 5px solid rgba(255, 255, 255, 0.07);
    width: 100vw;
    height: 100vh;
  }
  .menu__list {
    flex-direction: column;
  }
  .menu__item {
    padding-left: 0;
    padding-right: 0;
  }
  .menu__link--active::after {
    display: none;
  }
}

.rightside-menu {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  max-width: 694px;
  width: 100%;
  padding-top: 40px;
  background: var(--white);
  box-shadow: var(--bs);
  z-index: 5;
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.rightside-menu.rightside-menu--close {
  transform: translateX(500%);
}
.rightside-menu__close {
  padding: 10px;
  margin-left: 50px;
  margin-bottom: 30px;
  text-align: left;
}
.rightside-menu__content {
  max-width: 485px;
  margin: 0 auto;
  text-align: center;
}
.rightside-menu__logo {
  display: inline-block;
  margin-bottom: 40px;
}
.rightside-menu__title {
  font-size: 36px;
  line-height: 1.4166666667;
  color: var(--title);
}
.rightside-menu__line {
  width: 252px;
  height: 1px;
  margin-bottom: 17px;
  background: var(--line);
  border: 1px solid var(--line);
}
.rightside-menu__text {
  margin-bottom: 75px;
}
.rightside-menu__img {
  max-width: 485px;
}

@media screen and (width < 1024px) {
  .rightside-menu.rightside-menu--close {
    display: none;
  }
}
.logo {
  width: clamp(6.125rem, 5.484rem + 3.21vw, 9.25rem);
  flex-shrink: 0;
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .logo:hover {
    transform: scale(1.2);
  }
}
@media screen and (width > 541px) {
  .logo {
    margin: 0 auto;
  }
}
@media screen and (width < 541px) {
  .logo {
    order: -1;
    position: relative;
    z-index: 12;
  }
}

.breadcrumbs {
  background: rgba(223, 223, 223, 0.5);
  position: relative;
  z-index: 3;
  font-size: 16px;
}
.breadcrumbs__list {
  display: flex;
  gap: 5px;
  overflow-x: auto;
}
.breadcrumbs__item {
  display: flex;
  align-items: center;
}
.breadcrumbs__item + .breadcrumbs__item::before {
  content: "/";
  margin-left: -4px;
  margin-right: 5px;
}
.breadcrumbs__link {
  padding: 14px 0;
  color: var(--default);
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .breadcrumbs__link:hover {
    color: var(--accent);
  }
}
.breadcrumbs--no-bg {
  background-color: transparent;
}

.subtitle {
  position: relative;
  padding-bottom: 20px;
  font-family: var(--second-family);
  font-weight: 700;
  font-size: clamp(1.438rem, 1.275rem + 0.81vw, 2.25rem);
  text-align: center;
  color: var(--title);
  line-height: 167%;
}
.subtitle::after {
  position: absolute;
  content: "";
  width: 200px;
  height: 2px;
  left: 0;
  bottom: 0;
  background-color: var(--line);
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 1.2s ease;
}
.subtitle--visible::after {
  transform: scaleX(1);
}
.subtitle__fourth {
  font-size: clamp(1.125rem, 0.933rem + 0.96vw, 2rem);
  font-size: clamp(1.125rem, 1.043rem + 0.41vw, 1.5rem);
  line-height: 158%;
  color: var(--title);
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.subtitle__five {
  font-weight: 700;
  font-size: clamp(0.875rem, 0.738rem + 0.68vw, 1.5rem);
  line-height: 144%;
  color: var(--title);
}

.forms-button {
  min-width: 160px;
  padding: 6px 12px;
  font-weight: 600;
  background-color: var(--title);
  border-radius: 21px;
  color: var(--site-white);
  border: 1px solid transparent;
  align-self: flex-start;
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .forms-button:hover {
    color: var(--title);
    background-color: var(--accent);
    text-shadow: var(--text-shadow);
    -webkit-text-stroke: 1px #000;
    border: 1px solid var(--title);
  }
}

.slick-dots {
  display: flex;
  justify-content: center;
  gap: 30px;
}
.slick-dots button {
  font-size: 0;
  width: 8px;
  height: 8px;
  background-color: var(--title);
  border-radius: 50%;
}
@media screen and (width < 551px) {
  .slick-dots {
    gap: 20px;
  }
}
@media screen and (width < 376px) {
  .slick-dots {
    gap: 5px;
  }
  .slick-dots button {
    width: 4px;
    height: 4px;
  }
}

.slick-active button {
  background-color: #FFEF35;
}

.icon {
  display: block;
  fill: transparent;
  stroke: transparent;
  color: currentColor;
  fill: currentColor;
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  flex-shrink: 0;
}

.icon--menu {
  width: 37px;
  height: 29px;
}

.icon--chevron-down {
  width: clamp(1.563rem, 1.25rem + 1.56vw, 3.125rem);
  height: clamp(1.563rem, 1.25rem + 1.56vw, 3.125rem);
}

.icon--inst {
  width: 12px;
  height: 12px;
}

.icon--fb {
  width: 24px;
  height: 24px;
}

.icon--you {
  width: 12px;
  height: 10px;
}

.icon--phone {
  display: inline-block;
  margin-right: 9px;
  width: 14px;
  height: 14px;
}

.icon--arrow-left,
.icon--arrow-right {
  display: inline-block;
  width: clamp(1.25rem, 0.98rem + 1.35vw, 2.5rem) !important;
  height: clamp(0.875rem, 0.74rem + 0.68vw, 1.5rem) !important;
}

.icon--arrow-down {
  display: inline-block;
  margin-left: 16px;
  width: 8px;
  height: 13px;
}

.icon-measurements,
.icon-designer,
.icon-production {
  position: relative;
  flex-shrink: 0;
  width: 71px;
  height: 72px;
}

.icon-designer .icon-top,
.icon-designer .icon-bottom {
  position: absolute;
  left: 0;
  width: 71px;
  opacity: 0;
}
.icon-designer .icon-top {
  top: 0;
  height: 52px;
}
.icon-designer .icon-top.animate {
  animation: slide-in-top 2s forwards;
}
.icon-designer .icon-bottom {
  bottom: 0;
  height: 17px;
}
.icon-designer .icon-bottom.animate {
  animation: slide-in-bottom 3s forwards;
}

@keyframes slide-in-top {
  from {
    transform: translateY(-32px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes slide-in-bottom {
  from {
    transform: translateY(24px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.icon-measurements .icon-small,
.icon-measurements .icon-medium,
.icon-measurements .icon-large {
  position: absolute;
  transform: translate(-50%, -50%);
  opacity: 0;
}
.icon-measurements .icon-small {
  transform: translateX(-40px);
  width: 28px;
  height: 25px;
  top: 54%;
  left: 36%;
}
.icon-measurements .icon-small.animate {
  animation: slide-in-left 3s forwards;
}
.icon-measurements .icon-medium {
  transform: translateX(40px);
  width: 32px;
  height: 32px;
  top: 50%;
  left: 36%;
}
.icon-measurements .icon-medium.animate {
  animation: slide-in-right 1s forwards;
}
.icon-measurements .icon-large {
  transform: translateY(40px);
  top: 50%;
  left: 50%;
  width: 90px;
  height: 72px;
}
.icon-measurements .icon-large.animate {
  animation: slide-in-up 2s forwards;
}

@keyframes slide-in-left {
  from {
    transform: translate(-150%, -50%);
    opacity: 0;
  }
  to {
    transform: translate(-50%, -50%);
    opacity: 1;
  }
}
@keyframes slide-in-right {
  from {
    transform: translate(50%, -50%);
    opacity: 0;
  }
  to {
    transform: translate(-50%, -50%);
    opacity: 1;
  }
}
@keyframes slide-in-up {
  from {
    transform: translate(-50%, 50%);
    opacity: 0;
  }
  to {
    transform: translate(-50%, -50%);
    opacity: 1;
  }
}
.icon-production {
  opacity: 0;
  transform: scale(0.8);
}
.icon-production.animate {
  animation: fade-in-scale 4s ease-out forwards;
}

@keyframes fade-in-scale {
  0% {
    opacity: 0;
    transform: scale(0.8) rotate(-3deg);
  }
  50% {
    opacity: 0.5;
    transform: scale(1.05) rotate(1deg);
  }
  100% {
    opacity: 1;
    transform: scale(1) rotate(0deg);
  }
}
.social-media {
  display: flex;
  align-items: center;
  gap: 36px;
  color: var(--default);
}
.social-media__link {
  display: flex;
  align-items: center;
  color: inherit;
}
.social-media__link svg {
  transition: transform 0.6s ease;
  transform-origin: center;
}
@media (any-hover: hover) {
  .social-media__link:hover svg {
    scale: 1.5;
    transform: rotate(360deg);
  }
}

.info-email {
  padding-block: 6px;
  align-items: center;
  gap: 14px;
  color: var(--title);
}

.page-home .blog {
  padding-bottom: clamp(3.25rem, 2.612rem + 3.19vw, 6.375rem);
}
.page-home .blog__items {
  display: flex;
  justify-content: space-between;
  gap: 30px;
  margin-inline: auto;
  width: 100%;
}
.page-home .blog__item {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin-bottom: 0;
}
.page-home .blog-card__img-box,
.page-home .blog-card__video-box {
  height: 307px;
}
.page-home .blog-card__title-link {
  color: inherit;
  min-height: calc(clamp(1.125rem, 1.05rem + 0.38vw, 1.5rem) * 1.58 * 2);
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  min-height: calc(clamp(1.125rem, 1.05rem + 0.38vw, 1.5rem) * 1.58 * 2);
}
.page-home .blog-card__img {
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .page-home .blog-card__img:hover {
    transform: translate(-50%, -50%) scale(1.3);
  }
}
@media screen and (width < 769px) {
  .page-home .blog__title {
    text-wrap: balance;
  }
  .page-home .blog__items {
    flex-wrap: wrap;
  }
  .page-home .blog__item {
    width: 100%;
  }
}

.page-about .collection {
  padding-bottom: clamp(0.625rem, -1.198rem + 9.11vw, 5.125rem);
}

.page-blog {
  overflow-x: hidden;
  min-height: auto;
  height: 100vh;
}
.page-blog .blog__top {
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
}
.page-blog .blog__title {
  max-width: 430px;
  line-height: 142%;
}
.page-blog .blog .subtitle::after {
  display: none;
}
.page-blog .blog__items {
  padding-bottom: 18vh;
}
.page-blog .blog__item {
  border-radius: var(--border-radius);
  transform-origin: center top;
  transition: transform 0.3s ease-out, z-index 0s linear;
  will-change: transform;
  isolation: isolate;
  z-index: 1;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  padding: 10px;
  background-color: var(--white);
  position: sticky;
  top: 2rem;
}
.page-blog .blog-card p {
  line-height: 150%;
  color: var(--dark-gray);
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  line-clamp: 4;
}

.page-blog-one .blog__item {
  margin-bottom: 70px;
  opacity: 1;
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.page-blog-one .blog__item.fade-out {
  opacity: 0;
}
.page-blog-one .blog__item.fade-in {
  opacity: 1;
}

.page-contacts {
  min-height: auto;
  height: 100vh;
}
.page-contacts .social-media {
  gap: 46px;
}